header {
  align-items: center;
  background-color: #201a1b;
  color: white;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 24px 32px;
}

.header-logo {
  align-items: center;
  display: flex;
  gap: 12px;
}

.header-right {
  align-items: center;
  display: flex;
  gap: 32px;
}

nav {
  display: flex;
  flex-wrap: wrap;
  font-size: 1.4rem;
  font-weight: 600;
  gap: 16px;
  text-transform: lowercase;
}

header a {
  color: white;
  text-decoration: none;
  transition: 0.3s;
}

header a:hover {

  color: #ffb0c8;
}

.header-profileContainer {
  align-items: center;
  background-color: #5a3f46;
  border-radius: 32px;
  color: white;
  display: flex;
  font-weight: 600;
  gap: 8px;
  padding: 8px 12px;
}

.header-profile-img {
  border-radius: 50%;
  width: 24px;
}
