.searchContainer {
  align-items: center;
  display: flex;
  flex-direction: column;
  gap: 32px;
}

.searchContainer form {
  display: flex;
  gap: 16px;
  margin-top: 64px;
}

.searchArtistInput {
  min-width: 400px;
}

.searchContainer button {
  padding: 12px 36px;
}

.cardsContainer {
  display: flex;
  flex-wrap: wrap;
  gap: 32px;
  justify-content: center;
}

@media screen and ( max-width : 768px ) {

  .searchContainer form {
    flex-wrap: wrap;
  }

  .searchContainer button {
    flex: 1;
  }

  .searchArtistInput {
    min-width: unset;
    width: 100%;
  }
}
